import axios from 'axios';

const url = '/api/client';

class ClientService {
    // Get Data

    static getClient() {
        let data = {};
        return new Promise((resolve, reject) => {
            try {
                (async function() {
                    await setData('', data, 'client');
                    await setData('user', data, 'user');
                    console.log(data);
                    resolve(data);
                })();
            } catch (er) {
                reject(er);
            }
        });
    }

    static getData(extraUrl) {
        return new Promise((resolve, reject) => {
            try {
                axios.get(`${url}${extraUrl}`).then(res => {
                    resolve(res.data);
                })
            } catch (error) {
                reject(error);
            }
        });
    }
}

async function setData(extraLink, obj, name) {
    return new Promise((resolve, reject) => {
        try {
            axios.get(`${url}/${extraLink}`).then(res => {
                if(obj && name)
                    resolve(obj[name] = res.data);
                else resolve(res.data);
            });
        } catch (err) {
            reject(err);
        }
    });
}


export default ClientService;